import {AfterViewInit, Component, ViewChild} from '@angular/core';

import {IonSlides, Platform} from '@ionic/angular';
import { SplashScreen } from '@ionic-native/splash-screen/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
declare const Client: any;
declare const prefs: any;
@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss']
})
export class AppComponent implements AfterViewInit {
  @ViewChild(IonSlides) slides: any;
  fontSize = '30px';
  tfontSize = '35px';
  pfontSize = '30px';
  bfontSize = '30px';
  ycolor = '';
  ncolor = '';
  ypos = '';
  npos = '';
  bheight = '';
  bwidth = '';

  count = 0;
  title = "";
  failedQuestions = [];
  question = [];
  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar
  ) {
    this.initializeApp();
    let tmpArray = [];
    tmpArray.push("")

    if(prefs){
      this.title = prefs.getString('title');
      this.fontSize = prefs.getString('fontsize');
      this.tfontSize = prefs.getString('tfontsize');
      this.pfontSize = prefs.getString('pfontsize');
      this.bfontSize = prefs.getString('bfontsize');
      this.ycolor = prefs.getString('ycolor');
      this.ypos = prefs.getString('ypos');
      this.bheight = prefs.getString('bheight');
      this.bwidth = prefs.getString('bwidth');
      this.ncolor = prefs.getString('ncolor');
      this.npos = prefs.getString('npos');
      for(let index = 0; index<10; index++){
        console.log(prefs.getString('q'+(index+1)), 'q'+(index+1))
        tmpArray.push(prefs.getString('q'+(index+1)))
      }

    } else {
      console.log('prefs not valid')
    }
    this.question = tmpArray.filter((a=>{if(a) return a}))
    console.log(this.question)
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }


  toNext(val){
    if(val) this.failedQuestions.push(val);
    this.slides.lockSwipeToNext(false);
    this.slides.lockSwipeToPrev(false);
    this.count++;
    if(this.count==this.question.length){
      //output to client
      if (typeof Client != 'undefined') {
        Client.callback(JSON.stringify(this.failedQuestions));
      }
      this.slides.slideTo(0);
      this.count = 0;
      this.failedQuestions = [];
    } else {
      this.slides.slideNext();
    }
    this.slides.lockSwipeToNext(true);
    this.slides.lockSwipeToPrev(true);

  }

  async ngAfterViewInit() {
    this.slides.lockSwipeToNext(true);
    this.slides.lockSwipeToPrev(true);
  }
}
